<template>
  <div>
    <h2>Login</h2>
    <p v-if="$route.query.redirect">
      You need to login first.
    </p>
    <form @submit.prevent="checkLogin(user)">
      <v-text-field v-model="user.email" label="Email" counter="25"></v-text-field>
      <v-text-field type="password" v-model="user.password" label="Password"></v-text-field>
      <v-btn class="primary" @click="checkLogin">Login</v-btn>(hint: manager)
      <v-alert v-if="error" class="error">Bad login information</v-alert>
    </form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      error: false,
      logout:false,
      isLoading:false,
      user:{email:'user',password:''}
    }
  },
  methods: {
    async checkLogin(){
      let user = await this.$store.dispatch('auth/checkLogin', this.user);
      let loggedIn = user.authenticated
      if (!loggedIn) {
        this.error = true
      } else {
        this.$router.replace(this.$route.query.redirect || '/')
      }
    }
  }
}
</script>

