import timetracker from '../../api/timetracker.api'

// initial state
const state = {
  timeEntries: [],
  timeEntryStatus: null
}

// getters
const getters = {
  getTimeEntries: (state) => {
    return state.timeEntries
  },

  getTotalPrice: (state, getters, rootState) => {
    return getters.getTimeEntries.reduce((total, timeEntry) => {
      return total + timeEntry.weekHrs * (rootState.employee.hourlyRate || 1)
    }, 0)
  },
  getIndex:(state,timeEntry)=>{    
    var itemIndex =  state.timeEntries.findIndex(item => item.id===timeEntry.id);
    return itemIndex;
  }
}

// actions
const actions = {
  getAllTimeEntries: ({ commit }) => {
    timetracker.getTimeEntries((timeEntrys) => {
      commit('SET_TIMEENTRIES', { timeEntries: timeEntrys })
    })
  },
  saveTimeEntries ({ commit , getters}, timeEntry) {
    commit('SET_TIMEENTRY_STATUS', null)
    timetracker.addTimeEntry(
      timeEntry,
      (timeEntri) => {
        commit('SET_TIMEENTRY_STATUS', 'successful')
        if(!timeEntry.id){
          timeEntry.id = timetracker.getNextId()
        }        
        commit('SAVE_TIMEENTRY', { timeEntry: timeEntri, getters })
      },
      () => {
        commit('SET_TIMEENTRY_STATUS', 'failed')
        // rollback to the cart saved before sending the request
        //commit('SET_TIMEENTRIES', { timeEntries: savedTimeEntries })
      }
    )
  },

  removeTimeEntry({ commit, getters }, {timeEntry}) {
    commit('SET_TIMEENTRY_STATUS', null)
    
    timetracker.removeTimeEntry(
      timeEntry,
      () => {
        commit('SET_TIMEENTRY_STATUS', 'successful')
        commit('POP_TIMEENTRY', { timeEntry, getters})
      },
      () => {
        commit('SET_TIMEENTRY_STATUS', 'failed')
      }
    )
  },
}

// mutations
const mutations = {
  POP_TIMEENTRY (state, timeEntry) {
    var itemIndex =  state.timeEntries.findIndex(item => item.id===timeEntry.id);
    state.timeEntries.splice(itemIndex,1);
  },

  SAVE_TIMEENTRY (state, { timeEntry }) {
    var itemIndex =  state.timeEntries.findIndex(item => item.id===timeEntry.id);
    timeEntry.weekHrs = 
      (+timeEntry.day1Hrs ||0) +
      (+timeEntry.day2Hrs ||0) +
      (+timeEntry.day3Hrs ||0) +
      (+timeEntry.day4Hrs ||0) +
      (+timeEntry.day5Hrs ||0) +
      (+timeEntry.day6Hrs ||0) +
      (+timeEntry.day7Hrs ||0) ;
    if(itemIndex<0){
      state.timeEntries.push(timeEntry)
    }else{     
      Object.assign(state.timeEntries[itemIndex], timeEntry)
    }
  },
  
  SET_TIMEENTRIES (state, { timeEntries }) {
    state.timeEntries = timeEntries
  },

  SET_TIMEENTRY_STATUS (state, status) {
    state.timeEntryStatus = status
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}