import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Timesheet from './views/Timesheet.vue'
import Shopping from './components/ShopperApp.vue'
import Fnf404 from './components/auth/Fnf404.vue'
import Ang503 from './components/auth/Ang503.vue'
import Login from './components/auth/Login.vue'
import Todo from './views/Todo.vue'

Vue.use(Router)

 const requireAuth = (to, from, next) => {
  if (!localStorage.token) {
    next({
      path: '/login',
      query: { redirect: to.fullPath }
    })
  } else {
    next()
  }
}
const router = new Router({
  routes: [
    { path: '/', name: 'home', component: Home, beforeEnter: requireAuth },    
    { path: '/shoppers', name: 'shopping', component: Shopping , beforeEnter: requireAuth},
    { path: '/forbidden/timesheet', name: 'timesheet', component: Timesheet},
    { path: '/todos', name: 'todos', component: Todo , beforeEnter: requireAuth},
    { path: '/404', name: '404', component: Fnf404},
    { path: '/503', name: '503', component: Ang503},
    { path: '/login', name: 'login', component: Login},
    { path: '/logout', name: 'logout', beforeEnter:(to, from, next)=>{ next({name:'login', params:{logout:true}}) }},
    { path: '/about', name: 'about', 
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    },
    { path: '/**', name: 'none', redirect:'404'},
  ]
})

router.beforeEach((to, from, next) =>{
  if (to.path.indexOf('forbidden')>=0) {
    next('503')
  } else {
    next()
  }
})

export default router
