<template>
  <div class="cart">
    <h2>Your Cart</h2>
    <p v-show="!products.length"><i>Please add some products to cart.</i></p>

    <v-list  v-show="products.length">
      <v-list-item v-for="product in products" :key="product.id">
        <v-list-item-content >
          <v-list-item-title> 
            {{ product.title }} 
          </v-list-item-title >  
          <v-list-item-subtitle> {{ product.price | currency }} 
            <span class="text-right" right> 
              <v-chip small class="ma-2" color="green" text-color="white">{{ product.quantity }}</v-chip>
            </span>
          </v-list-item-subtitle>         
        </v-list-item-content>    
        <v-list-item-icon :disabled="!product.inventory" @click="removeProductFromCart(product)">
          <v-icon class="pointer">mdi-minus</v-icon>
        </v-list-item-icon>  
      </v-list-item>
    </v-list>
    <p>Total: {{ total | currency }}</p>
    <p><v-btn :disabled="!products.length" @click="checkout(products)" class="primary">Checkout</v-btn></p>
    <p v-show="checkoutStatus">Checkout {{ checkoutStatus }}.</p>


  </div>

</template>

<script>
import { mapGetters, mapState, mapActions } from 'vuex'
export default {
  computed: {
    ...mapState({
      checkoutStatus: state => state.cart.checkoutStatus
    }),
    ...mapGetters('cart', {
      products: 'cartProducts',
      total: 'cartTotalPrice'
    })
  },
  methods: {
    checkout (products) {
      this.$store.dispatch('cart/checkout', products)
    },
    ...mapActions('cart',['removeProductFromCart'])
  }
}
</script>