<template>
<div>
  <v-data-table :headers="headers" :items="getTimeEntries" sort-by="weekNum" class="elevation-1" >
    <template v-slot:top>
      <v-toolbar flat color="white">
        <v-toolbar-title>Time Tracker (<small style="color:orangered">{{getTotalPrice}}</small>Hrs) for <small style="color:orangered">{{employee.vendorCode}}</small>  </v-toolbar-title> 
        <v-divider class="mx-4" inset vertical ></v-divider>
        <v-spacer></v-spacer> 
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">New Time Entry</v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm6 md4>
                    <v-select :items="sows" item-text="title" item-value="id" v-model="editedItem.sowId" label="SOW"></v-select>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-select :items="invs" item-text="title" item-value="id" v-model="editedItem.invId" label="Investment"></v-select>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-select :items="delvs" item-text="title" item-value="id" v-model="editedItem.delvId" label="Deliverables"></v-select>
                  </v-flex>
                  <v-flex xs12 sm8 md8>
                    <v-text-field v-model="editedItem.task" label="Task" counter="25"></v-text-field>
                  </v-flex>
                  <v-flex xs6 sm4 md4>
                    <v-select :items="weeks" item-text="label" item-value="value" v-model="editedItem.weekNum" label="Week"></v-select>
                    <!-- <v-text-field v-model.number="editedItem.weekNum" type="hidden"></v-text-field>
                    <v-label text-right >{{getDateRangeOfWeek(editedItem.weekNum,editedItem.yearNum)}}</v-label> -->
                  </v-flex>
                  <v-flex xs6 sm3 md2>
                    <v-text-field text-right type="number" v-model.number="editedItem.day1Hrs" :label="getDateOfWeek(editedItem.weekNum,editedItem.yearNum, 0)"></v-text-field>
                  </v-flex>
                  <v-flex xs6 sm3 md2>
                    <v-text-field text-right type="number" v-model.number="editedItem.day2Hrs" :label="getDateOfWeek(editedItem.weekNum,editedItem.yearNum, 1)"></v-text-field>
                  </v-flex>
                  <v-flex xs6 sm3 md2>
                    <v-text-field text-right type="number" v-model.number="editedItem.day3Hrs" :label="getDateOfWeek(editedItem.weekNum,editedItem.yearNum, 2)"></v-text-field>
                  </v-flex>
                  <v-flex xs6 sm3 md2>
                    <v-text-field text-right type="number" v-model.number="editedItem.day4Hrs" :label="getDateOfWeek(editedItem.weekNum,editedItem.yearNum, 3)"></v-text-field>
                  </v-flex>
                  <v-flex xs6 sm3 md2>
                    <v-text-field text-right type="number" v-model.number="editedItem.day5Hrs" :label="getDateOfWeek(editedItem.weekNum,editedItem.yearNum, 4)"></v-text-field>
                  </v-flex>
                  <v-flex xs6 sm3 md2>
                  </v-flex>
                  <v-flex xs6 sm3 md2>
                    <v-text-field text-right type="number" v-model.number="editedItem.day6Hrs" :label="getDateOfWeek(editedItem.weekNum,editedItem.yearNum, 5)"></v-text-field>
                  </v-flex>
                  <v-flex xs6 sm3 md2>
                    <v-text-field text-right type="number" v-model.number="editedItem.day7Hrs" :label="getDateOfWeek(editedItem.weekNum,editedItem.yearNum, 6)"></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn class="primary" @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.action="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)" color="blue darken-1" > mdi-pencil </v-icon>
      <v-icon small @click="deleteItem(item)"  color="red lighten-1" >mdi-delete </v-icon>
    </template>
    <template v-slot:item.sowId="{ item }">
      <v-chip >{{ getSowName(item.sowId) }}</v-chip>
    </template>
    <template v-slot:item.invId="{ item }">
      <v-chip >{{ getInvName(item.invId) }}</v-chip>
    </template>
    <template v-slot:item.delvId="{ item }">
      <v-chip >{{ getDelvName(item.delvId) }}</v-chip>
    </template>
    <template v-slot:item.weekNum="{ item }">
      <v-chip >{{ getWeekDateRange(item.weekNum, item.yearNum) }}</v-chip>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="initialize">mdi-Reset</v-btn>
    </template>
  </v-data-table>  
</div>
</template>
<script>
import { mapGetters, mapActions  } from 'vuex'
Date.prototype.getWeek = function() {
  var date = new Date(this.getTime());
  date.setHours(0, 0, 0, 0);
  // Thursday in current week decides the year.
  date.setDate(date.getDate() + 3 - (date.getDay() + 6) % 7);
  // January 4 is always in week 1.
  var week1 = new Date(date.getFullYear(), 0, 4);
  // Adjust to Thursday in week 1 and count number of weeks from date to week1.
  return 1 + Math.round(((date.getTime() - week1.getTime()) / 86400000 - 3 + (week1.getDay() + 6) % 7) / 7);
}
//const weekdaysP = ["Mon","Tues","Wednes","Thur","Fri","Satur","Sun"]
const weekdays = ["Mon","Tue","Wed","Thu","Fri","Sat","Sun"]
export default {
    data: () => ({
      dialog: false,
      headers: [
        { text: 'Sow', value: 'sowId', align: 'left', sortable: false },
        { text: 'Inv', value: 'invId', sortable: false},
        { text: 'Deliv', value: 'delvId' },
        { text: 'Task', value: 'task' },
        { text: 'Week', value: 'weekNum' },
        { text: 'Monday',     value: 'day1Hrs' , align: 'right' },
        { text: 'Tueday',     value: 'day2Hrs' , align: 'right' },
        { text: 'Wednesday',  value: 'day3Hrs' , align: 'right' },
        { text: 'Thursday',   value: 'day4Hrs' , align: 'right' },
        { text: 'Friday',     value: 'day5Hrs' , align: 'right' },
        { text: 'Saturday',   value: 'day6Hrs' , align: 'right' },
        { text: 'Sunday',     value: 'day7Hrs' , align: 'right' },        
        { text: 'subTotal',   value: 'weekHrs' , align: 'right' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      timeEntries: [],
      weeksofYear:[],
      editedIndex: -1,
      editedItem: {        
        employeeId: 0,
        sowId: 0,
        invId: 0,
        delvId: 0,
        task: '',
        weekNum: 0,
        yearNum: 0,
        monthNum: 0,
        day1Hrs: 0,
        day2Hrs: 0,
        day3Hrs: 0,
        day4Hrs: 0,
        day5Hrs: 0,
        day6Hrs: 0,
        day7Hrs: 0,
        weekHrs: 0.0
      },
      defaultItem: {      
        employeeId: 0,
        sowId: 0,
        invId: 0,
        delvId: 0,
        task: '',
        weekNum: 0,
        yearNum: 0,
        monthNum: 0,
        day1Hrs: 0,
        day2Hrs: 0,
        day3Hrs: 0,
        day4Hrs: 0,
        day5Hrs: 0,
        day6Hrs: 0,
        day7Hrs: 0,
        weekHrs: 0.0
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Time Entry' : 'Edit Time Entry'
      },/* 
      isLoggedInUser () {
        // Check in the getter if the staff is happy
        return this.$store.getters.isLoggedInG
      }, */
      ...mapGetters('timetracker', [
        'getTimeEntries',
        'getTotalPrice'
      ]),
      ...mapGetters('employee', {
        // map `this.doneCount` to `this.$store.getters.doneTodosCount`
        employee : 'getEmployeeData'
      }),
      sows(){
        let items = [];
        items.push(...this.employee.sows)  
        return items
      },
      invs(){
        let items = [];
        items.push(...this.employee.invs)
        return items
      },
      delvs(){
        let items = [];
        items.push(...this.employee.delvs)
        return items
      }  ,
      weeks(){
        let items = [];
        items.push(...this.weeksofYear)
        return items
      } 
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      getDateRangeOfWeek(weekNo, y){
        var d1, numOfdaysPastSinceLastMonday, rangeIsFrom, rangeIsTo;
        d1 = new Date(''+y+'');
        numOfdaysPastSinceLastMonday = d1.getDay() - 1;
        d1.setDate(d1.getDate() - numOfdaysPastSinceLastMonday);
        d1.setDate(d1.getDate() + (7 * (weekNo - d1.getWeek())));
        rangeIsFrom = (d1.getMonth() + 1) + "-" + d1.getDate();
        d1.setDate(d1.getDate() + 6);
        rangeIsTo = (d1.getMonth() + 1) + "-" + d1.getDate() ;
        return rangeIsFrom + "[to]" + rangeIsTo + " ("+d1.getFullYear()+")";
      },
      getDateOfWeek(weekNo, y, i){
        var d1, numOfdaysPastSinceLastMonday;//, rangeIsFrom, rangeIsTo;
        d1 = new Date(''+y+'');
        numOfdaysPastSinceLastMonday = d1.getDay() - 1;
        d1.setDate(d1.getDate() - numOfdaysPastSinceLastMonday);
        d1.setDate(d1.getDate() + (7 * (weekNo - d1.getWeek())));
        //rangeIsFrom = (d1.getMonth() + 1) + "-" + d1.getDate() + "-" + d1.getFullYear();
        d1.setDate(d1.getDate() + i);
        //rangeIsTo = (d1.getMonth() + 1) + "-" + d1.getDate() + "-" + d1.getFullYear() ;
        //return rangeIsFrom + " to " + rangeIsTo;
        return weekdays[i]+ ", " +(d1.getMonth() + 1) + "/" + d1.getDate() ;//+ "-" + d1.getFullYear() 
      },
      getWeekDateRange(weekNum){
        let wik = this.weeks.find(week=> week.value===weekNum);
        return wik ? wik.label : '';
      },
      getInvName(id){
        let inv = this.invs.find(inv=> inv.id===id);
        return inv ? inv.title : '';
      },
      getSowName(id){
        let sow = this.sows.find(sow=> sow.id===id);
        return sow ? sow.title : '';
      },
      getDelvName(id){
        let delv = this.delvs.find(delv=> delv.id===id);
        return delv ? delv.title : '';
      },
      ...mapActions('timetracker',['getAllTimeEntries','saveTimeEntries','removeTimeEntry']),
      initialize () {
        this.getAllTimeEntries()//timeEntries=>this.timeEntries=timeEntries)
        let today = new Date();
        let getCurrentWeek = +today.getWeek();
        let getCurrentYear = +today.getFullYear();
        let weekWindow = +10;
        for ( let i=-weekWindow;i<=weekWindow;i++){
          let itWeek = getCurrentWeek + i;
          let itYear = (itWeek < 1 )? (getCurrentYear - 1) : (itWeek > 52 ? getCurrentYear + 1 : getCurrentYear)  ;
          itWeek = (itWeek < 0 )? (52- itWeek) : (itWeek > 52 ? 52 % itWeek : itWeek) ;
          //[i+weekWindow]
          this.weeksofYear.push({value: itWeek, year:itYear, label: this.getDateRangeOfWeek(itWeek, itYear)});
        }
        
      },

      editItem (item) {
        this.editedIndex = this.timeEntries.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.timeEntries.indexOf(item)
        if(confirm('Are you sure you want to delete this item?')){ 
          this.removeTimeEntry(item);
          this.timeEntries.splice(index, 1) 
        }
      },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.timeEntries[this.editedIndex], this.editedItem)
          this.saveTimeEntries(this.editedItem)
        } else {
          this.saveTimeEntries(this.editedItem)
          this.timeEntries.push(this.editedItem)
        }
        this.close()
      }
    },
  }
</script>
