// initial state
const state = {
  isLoggedIn :!!localStorage.token,
  loggedUser:JSON.parse(localStorage.user||'{}'),
  username:''
}

// getters
const getters = {
  getToken (state) {
    return state.loggedUser.token
  },
  getCurrentUser (state) {
    return state.loggedUser;//localStorage.user ? JSON.parse(localStorage.user) :{authenticated:false}
  },
  loggedIn (state) {
    return state.loggedUser.authenticated
  },
  getUsername(state){
    return state.username
  }
}

// actions
const actions = {
  async checkLogin ({ commit }, user) {
    if (localStorage.token && localStorage.user) {   
      let user = JSON.parse(localStorage.user)   
      commit("ON_CHANGE", true)
      commit("SET_USER", user)
      commit("SET_USERNAME", user.username)
      return user
    }
    let resUser = await pretendRequest(user)
    if (resUser.authenticated) {
      localStorage.token = resUser.token   
      localStorage.user = JSON.stringify(resUser)
      commit("SET_USER", resUser)
      commit("SET_USERNAME", resUser.username)
      commit("ON_CHANGE", true)
    } else {    
      commit("SET_USER", user)
      commit("SET_USERNAME", '')
      commit("ON_CHANGE", false)
    }
    return  resUser
  },
  logout ({ commit }) {
    delete localStorage.token
    delete localStorage.user
    commit("SET_USER", {})
    commit("SET_USERNAME", '')
    
    commit("ON_CHANGE", false)
  },
  setCurrentUser({ commit }, user) {
    commit("SET_USER", user)
  }
}

// mutations
const mutations = {
  ON_CHANGE (state, isChanged) {
    state.isLoggedIn = isChanged
  },
  SET_USER : (state, user) => {
    delete user.password
    user.authenticated = !!user.authenticated;
    state.loggedUser = Object.assign({},state.loggedUser,user)
  },
  SET_USERNAME: (state, username) => {
    state.username = username
  }
}

const pretendRequest = async ({email, password}) => {
  let roles = ['ROLE_USER', 'ROLE_ADMIN', 'ROLE_MANAGER']
  let res = await validateUser(email, password, roles);
  
  return res
}
const validateUser = (email, password, roles)=>{
  return new Promise(resolve => {
    setTimeout(() => {
      let user ;
      if (email === 'admin' && password === 'admin') {
        user = {
          email,
          username:'Admin',
          roles,
          authenticated: true,
          token: Math.random().toString(36).substring(7)
        }
      } else if (email === 'manager' && password === 'manager') {
        user = {
          email,
          username:'Manager',
          roles: roles.filter(item=>item!=='ROLE_ADMIN'),
          authenticated: true,
          token: Math.random().toString(36).substring(7)
        }
      } else if (email === 'user' && password === 'user') {
        user = {
          email,
          username:'A0123451',
          roles : roles.filter(item=>item==='ROLE_USER'),
          authenticated: true,
          token: Math.random().toString(36).substring(7)
        }
      } else {
        user = { authenticated: false }
      }
      resolve(user)
    }, Math.random()*900+1000)
  })
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}