import todoApi from '../../api/todo.api'
export default {
  namespaced: true,
  state: {
    todos: []
  },
  getters:{
    getTodos (state) {
      return state.todos 
    },
    getCount: state => {
      return state.todos.length
    },
    doneTodos: state => {
      return state.todos.filter(todo => todo.done)
    },
    doneTodosCount: (state, getters) => {
      return getters.doneTodos.length
    },
    getTodoById: (state) => (id) => {
      return state.todos.find(todo => todo.id === id)
    },
  },
  mutations: {
    ADD_TODO (state, todo) {
      state.todos.push(todo)
    },
    SET_TODOS (state, todos) {
      state.todos = todos
    },
    REMOVE_TODO(state, todo){
      let index = state.todos.findIndex(item=> item.task===todo.task)
      state.todos.splice(index,1)
    }
  },
  actions: {
    getAllTodos ({ commit }) {
      todoApi.getAllTodos(todos => {
        commit('SET_TODOS', todos)
      })
    },
    addTodo ({ commit }, todo) {
      let nextTodoId = todoApi.getNextTodoId();
      commit('ADD_TODO', {id: nextTodoId,task: todo.task})
      
    },
    removeTodo({commit},task){
      commit('REMOVE_TODO', task)
    }
  }
}