/**
 * Mocking client-server processing
 */
let todoIdCounter = 3
const _todos = [
  {"id": 1, "task": "Do exersize", done:true},
  {"id": 2, "task": "Buy food", done:false},
  {"id": 3, "task": "Prepare bed", done:false}
]

export default {
  getAllTodos (cb) {
    setTimeout(() => cb(_todos), 100)
  },
  getNextTodoId(){
    todoIdCounter+=1;
    return todoIdCounter
  },
  addTodo (todo, cb, errorCb) {
    setTimeout(() => {
      // simulate random checkout failure.
      if((Math.random() > 0.5 || navigator.userAgent.indexOf('PhantomJS') > -1)){
        const newTodo = {id:this.getNextTodoId(), task: todo.task, done:false};
         cb(newTodo)
      }else{
        cb(errorCb)
      }
    }, Math.random()*900+100) //Math.random()*900+
  }
}