<template>
<div class="todos">
  <h2>Todos</h2>

  <form novalidate column @submit.prevent="submit">
    <v-text-field v-model="task" v-validate="'required|max:20'" :counter="20" :error-messages="errors.collect('task')" 
    label="Task" data-vv-name="task" required ></v-text-field>
    <v-btn class="mr-4" @click="submit"><v-icon color="primary darken-2">mdi-plus</v-icon></v-btn>
    <v-btn @click="clear"><v-icon color="orange darken-2">mdi-eraser</v-icon></v-btn>
  </form>
  <v-list v-if="getCount>0">
    <v-list-item v-for="todo in getTodos" :key="todo.id" >
      <v-list-item-content >
        {{todo.task}}
      </v-list-item-content>
      <v-list-item-action >
        <v-list-item-icon @click="edit(todo)">
          <v-icon color="blue">mdi-pencil</v-icon>
        </v-list-item-icon>
      </v-list-item-action >
      <v-list-item-action >
        <v-list-item-icon @click="remove(todo.task)">
          <v-icon color="red">mdi-delete</v-icon>
        </v-list-item-icon>
      </v-list-item-action>
    </v-list-item>
  </v-list>
  <p v-else> Please add task! </p>
</div>

</template>
<style scoped>
  .v-list-item__icon{
        margin: 8px 0;
  }
  .v-list-item .v-list-item__action {
      visibility:hidden;
  }
  .v-list-item:hover {
    cursor: pointer;
    background:whitesmoke
  }
  .v-list-item:hover .v-list-item__action {
      visibility:visible;
  }
  .v-list-item__action .v-list-item__icon{
    display: inline-block
  }
  .v-list-item {
    border: 1px solid whitesmoke;
  }
</style>

<script>
export default {
  data:()=>{
    return {
      task:''
    }
  },
  computed : {
    getTodos(){
      return this.$store.getters["todos/getTodos"]
    },
    getCount(){
      return this.$store.getters["todos/getCount"]
    }
  },
  methods:{    
    clear () {
      this.task=''
    },
    submit () {
      this.$store.dispatch('todos/addTodo', {task:this.task})
       this.task=''
    },
    remove(task){
      if(confirm(`Are your sure to remove? "${task}"`))
      this.$store.dispatch('todos/removeTodo', {task})
    },
    edit(todo){
      this.task=todo.task
    }
  },
  created(){
    this.$store.dispatch('todos/getAllTodos')
  },
}
</script>


