import timetrackerApi from '../../api/timetracker.api'

const state = {
  employee: {
    hourlyRate:28,
    name:'Bhushan Kamathe',
    vendorCode:'A012345',
    sows:[
      { id:400, code: 433, title: 'XF Syntax', totalValue: 55123, startDate: new Date('2019-06-01'), endDate: new Date('2019-08-31')},
      { id:410, code: 443, title: 'AB Lyntax', totalValue: 54123, startDate: new Date('2019-06-01'), endDate: new Date('2019-08-31')},
    ],
    invs:[
      { id:401, code: 40031, title: 'Programming'},
      { id:402, code: 40032, title: 'Operations'}
    ],
    delvs:[
      { id:1, title: 'Development'},
      { id:2, title: 'Unit Test'}
    ],
    employeeCode: '12345'
  }
}

const getters = {
  getEmployeeData : (state) => {
    return state.employee
  },
}

const actions = {
  getEmployeeData : ({commit}) =>{
    timetrackerApi.getEmployeeData(employee => {
      commit('setEmployee', employee)
    })
  }
}
const mutations = {
  setEmployee : (state, employee) =>{
    state.employee = employee
  }
}

export default {
  namespaced:true,
  state,
  getters,
  actions,
  mutations
}