<template>
  <div>
    <v-navigation-drawer app dark v-model="drawer">
      
      <v-list dense ml=10px>
        <v-list-item >
          <v-list-item-content color="pink">
            <v-list-item-title class="title" color="pink">                          
              <span v-if="getAuthloggedIn" >        
                <span class="fr"  >{{getCurrentloginUser.username}}</span>
              </span>
              <span v-else>
                <span class="fr"  > GUEST</span>
              </span>
            </v-list-item-title>
            <v-list-item-subtitle>
              <span hidden class="small ">B2k</span>
            </v-list-item-subtitle>
          </v-list-item-content>

        </v-list-item>
        <v-divider/>
      </v-list >

      <v-list >
        <template v-for="item in items" >
          <v-list-item :key="item.title"  router :to="item.link" v-if="item.role==='ALL' || (getCurrentloginUser.roles && getCurrentloginUser.roles.indexOf(item.role)>=0)" >
            <v-list-item-content >
              <v-list-item-title>
                <v-icon>{{ item.icon }}</v-icon>
                <span class="link-title">{{ item.title }} </span>
                <v-badge right color="primary" class="mb-3" v-if="item.title=='Todos'"> 
                    <span slot="badge"> {{getCount}} </span>
                </v-badge>
              </v-list-item-title>
              
            </v-list-item-content>
          </v-list-item>
        </template >
        <v-list-item to="login" v-if="!getAuthloggedIn">
          <v-list-item-content >
            <v-list-item-title><v-icon>mdi-key</v-icon> Login</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

    </v-navigation-drawer>

    <v-app-bar app dark>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title class="headline">
        <span class="font-weight-light">b-Time</span>
        <span color="orangered">Tracker</span>
        <v-list-item-icon ><v-icon color="orangered" left>mdi-heart</v-icon></v-list-item-icon>
      </v-toolbar-title>     
      <span v-if="getAuthloggedIn" >        
        <span class="fr"  >{{getCurrentloginUser.username}}</span>
      </span>
      <span v-else>
        <span class="fr"  > GUEST</span>
      </span>   

      <v-spacer></v-spacer>
      <v-btn icon class="mr-3" @click="logoutUser"  v-if="getAuthloggedIn" title="Logout">
        <span class="mr-2"> <v-icon right>mdi-power</v-icon> </span>
      </v-btn>
    </v-app-bar>
    
  </div>  
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data:()=>{
    return {
      drawer : true,
      items:[
        { title: 'Home', icon: 'mdi-home' , link:'/', role:'ROLE_USER' },
        { title: 'About', icon: 'mdi-email', link:'/about', role:'ALL' },
        { title: 'Shopping', icon: 'mdi-cart', link:'/shoppers', role:'ROLE_ADMIN' },
        { title: 'Timesheet', icon: 'mdi-calendar', link:'/forbidden/timesheet', role:'ROLE_MANAGER' },
        { title: 'Reports', icon: 'mdi-clipboard-outline', link:'/reports', role:'ROLE_USER' },
        { title: 'Todos', icon: 'mdi-bell', link:'/todos', role:'ROLE_MANAGER' }
      ]
    }
  },
  computed: {
    ...mapGetters('todos',[
      'doneTodosCount',
      'getCount'
    ]),
    getAuthloggedIn (){
      return this.$store.getters['auth/loggedIn']
    },    
    getCurrentloginUser(){
      return this.$store.getters['auth/getCurrentUser']
    }
  },
  methods:{
    logoutUser(){
      this.$store.dispatch('auth/logout')
      this.$router.replace('/login')
    }
  }
}
</script>
<style scoped>
span.link-title {vertical-align: middle;padding: 0 16px;}
</style>