<template>
  <div class="products">
    <h2>Products</h2>
    <v-list >
      <v-list-item v-for="product in products" :key="product.id">
        <v-list-item-content >
          <v-list-item-title> 
            {{ product.title }} 
          </v-list-item-title>
          
          <v-list-item-subtitle> {{ product.price | currency }} 
            <span class="text-right" right> 
              
              <v-chip v-show="!!product.inventory" small class="ma-2" color="green" text-color="white">{{ product.inventory }}</v-chip>
              <v-chip v-show="!product.inventory" small class="ma-2" color="red" text-color="white">{{ product.inventory }}</v-chip>
            </span>
          </v-list-item-subtitle>         
        </v-list-item-content>
        <v-list-item-icon :disabled="!product.inventory" @click="addProductToCart(product)">
          <v-icon class="pointer">mdi-plus</v-icon>
        </v-list-item-icon>        
      </v-list-item>
    </v-list>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  computed: mapState({
    products: state => state.products.all
  }),
  methods: mapActions('cart', [
    'addProductToCart'
  ]),
  created () {
    this.$store.dispatch('products/getAllProducts')
  }
}
</script>